import Vue from 'vue'
import Vuex from 'vuex'

import {UserAccess} from "./plugins/user.js"

Vue.use(Vuex)

const axios = Vue.axios

const setToken = function(token){
  let value = token?`Bearer ${token}`:''
  axios.defaults.headers.common['Authorization']= value   
}

const defaultUser = {data:{type:0,username:'',email:''},
profile:{},token:false,downline:[ ],hasProfile:false}


export default new Vuex.Store({
  state: {
    hasProfile:false,
    loaders:{profile:false,downline:false},
    profile:{data:{},control:{loader:false,error:false,loaded:false}},
    user:{token:"",type:"",email:"",username:""},
    direct_downlines:{
      data:[],control:{loader:false,error:false,loaded:false}
    },
  },
  getters:{
    user: state => {return state.user.data},
    level:state => {return state.user.data.type},
    auth : state => {return state.user.token && true},
    downline:  (state) =>{return state.direct_downlines.data},
    downlineLoader: state =>{return state.direct_downlines.control.loader},
    downlineIsLoaded:state => {return state.direct_downlines.control.loaded},
    profile:state=> {return state.user.profile},
    profileLoader:state => {return state.profile.control.loader},
  },
  mutations: {
    updateAccess(state){
      state.user = UserAccess.getUserData()
      setToken(state.user.token)   

    },
    updateAccount(state,payload){
      UserAccess.login({token:state.user.token,...payload})

    },
    updateProfile(state,payload){
      if(state.user.type>=2){state.user.profile = {}}
      else{state.user.profile=payload}
      UserAccess.setProfile(state.user)

    },
    login(state,payload){
      UserAccess.login(payload)

    },    
    logOut(state){
      state.user = defaultUser
      setToken(state.user.token)

    },    
    routeNotify(state){
      return state.user.username && state.user.token
    },
    directDownlines(state,payload){
      state.direct_downlines.data = payload.children
    },downlineToggleLoader(state){
      state.direct_downlines.control.loader =
      !state.direct_downlines.control.loader
    },downlineControl(state,payload){
      state.direct_downlines.control = {...state.direct_downlines.control,...payload}
    }
  },
  actions: {
    async updateAccount(context){
      let response = {}
      try {
        response = await Vue.axios.get('user/profile')
        context.commit("updateAccount",response.data)
      } catch (error) {
        console.log(error);
      }
      context.commit('updateAccess')

    },
    async profileUpdate(context){
      let response = {}
      try {
        response = await Vue.axios.get('system/profile')
        
      } catch (error) {
        console.log(error);
        
      }
      context.commit("updateProfile",response.data)

    },async downlineUpdate(context){
      context.commit('downlineToggleLoader')
      let state = {error:true}
      let response = await Vue.axios.get('system/matrix')
      if(response.status=='200'){
        context.commit('directDownlines',response.data)
        state.error = false
      }
      state.loaded = true
      context.commit('downlineControl',state)
      context.commit('downlineToggleLoader')
    },
    logOut(context){
      UserAccess.logOut()
      context.commit('logOut')
    },
    login(context,data){
      context.commit('logOut')
      UserAccess.login(data)
      context.commit('updateAccess')
    }

  }
})
