import Vue from 'vue'
import Router from 'vue-router'
import store from './store.js'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      redirect:'/account'
    },{
      path:'/login',
      name:"login",
      component: () => import('./pages/Login.vue')
    },  {
      path: '/join',component:() => import('./pages/Signup.vue')
    },{
      path: '/join/:sponsor',
      name: "join",props:true,
      component: () => import('./pages/Signup.vue')
    },{
      path:"/account",
      name:"account",
      beforeEnter:(to,from,next)=>{
        store.commit("updateAccess")
        let access  = store.getters.auth
        if(access){
          if(to.name == "lock" || to.name=='payment'){next();return}
          let level = store.getters.level
          if (level>0){
            store.dispatch("profileUpdate").then(()=>{
              next()
              return
            })
          }else{next({name:'lock'})}
        }else{
          next({
            name:"login",query:{next:"account"},params:{message:"login to continue"}
          })
        }
      },
      component: () => import('./pages/Account.vue'),
      children:[
        { path: "", name: "account_home", component: () => import('./views/account/Home.vue') },
        { path: "matrix", name: "my_feeds", component: () => import('./views/account/Matrix.vue') },
        { path: "profile", name: "profile", component: () => import('./views/account/Profile.vue')},
        { path: "wallet", name: "wallet", component: () => import('./views/account/Wallet.vue')},
        { path: 'lock', name:'lock', component: ()=> import('./views/account/Lock.vue')},
        { path: 'payment', name:'payment', component: ()=> import('./views/account/Payment.vue')},
        { path: 'about', name:'about', component: ()=> import('./views/account/About.vue')},
      ]
    }
    
  ]
})
